import * as THREE from './../build/three.module.js'
import { RGBType, HSVType } from './colorSpaceConvertor.js'

let instance = null

class HSV {

    static getInstance() {
        if (instance == null) {
            instance = new HSV()
        }

        return instance
    }

    constructor() {

        const CENTRAL_X = 300.0
        const CENTRAL_Y = 100.0
        const WHEEL_RADIUS = 80
        const wheel_thick = 20;

        this.drawLittleTriangle = function (t) {

            let vertex = new Float32Array([
                100 + t.x, 300, 0,
                100 + t.x - 10, 300 - 15, 0,
                100 + t.x + 10, 300 - 15, 0
            ])

            let rgb = t.hsv.to_RGB();

            const geometry = new THREE.BufferGeometry()
            geometry.setAttribute('position', new THREE.BufferAttribute(vertex, 3))

            const material = new THREE.MeshBasicMaterial()
            material.setValues({color: new THREE.Color(rgb.R, rgb.G, rgb.B)})

            return new THREE.Mesh(geometry, material)
        }

        this.updateLittleTriangle = function (mylist, target) {
            let group = new THREE.Group()
            for (let i = 0; i < mylist.length; i++) {
                group.add(this.drawLittleTriangle(mylist[i]))
            }

            if (target != null) {
                let vertex = new Float32Array([
                    100 + target.x + 4, 300 - 25, 0,
                    100 + target.x + 4, 300 - 15, 0,
                    100 + target.x - 3, 300 - 15, 0,
                    100 + target.x + 4, 300 - 25, 0,
                    100 + target.x - 3, 300 - 15, 0,  
                    100 + target.x - 3, 300 - 25, 0
                ])

                let rgb = target.hsv.to_RGB()

                const geometry = new THREE.BufferGeometry()
                geometry.setAttribute('position', new THREE.BufferAttribute(vertex, 3))

                const material = new THREE.MeshBasicMaterial()
                material.setValues({ color: new THREE.Color(rgb.R, rgb.G, rgb.B) })
      
                group.add(new THREE.Mesh(geometry, material))
            }

            return group
        }

        function string_render(text, x, y, r, g, b, font) {
            const geometry = new THREE.TextGeometry(text, {
                font: font,
                size: 12
            })
            geometry.translate(x, y, 0)

            const material = new THREE.MeshBasicMaterial()
            material.setValues({ 'color': new THREE.Color(r, g, b) })

            return new THREE.Mesh(geometry, material)
        }

        let histogramText = null
        this.drawHistogram = function (font) {

            let geometry, material, vertex, group

            // ���
            if (histogramText == null) {
                let subGroup = new THREE.Group()

                for (let i = 0; i < 600; i += 100) {
                    subGroup.add(string_render(i.toString(), 1, i, 0.5, 0.5, 0.5, font))
                }
                
                histogramText = subGroup
            }

            group = new THREE.Group()
            group.add(histogramText.clone())

            // �έp�Ϫ��ؽu
            vertex = new Float32Array([
                100, 300 + 200, 0,
                100 + 256, 300 + 200, 0,
                100 + 256, 300 + 299, 0,
                100, 300 + 299, 0
            ])

            geometry = new THREE.BufferGeometry()
            geometry.setAttribute('position', new THREE.BufferAttribute(vertex, 3))

            material = new THREE.LineBasicMaterial()
            material.setValues({ 'color': 0x666666 })

            group.add(new THREE.LineLoop(geometry, material))

            vertex = new Float32Array([
                100, 300 - 15, 0,
                100 + 256, 300 - 15, 0
            ])

            geometry = new THREE.BufferGeometry()
            geometry.setAttribute('position', new THREE.BufferAttribute(vertex, 3))

            material = new THREE.LineBasicMaterial()
            material.setValues({ 'color': 0x666666 })

            //group.add(new THREE.LineSegments(geometry, material))

            vertex = new Float32Array([
                100, 300, 0,
                100 + 256, 300, 0,
                100 + 256, 300 + 180, 0,
                100, 300 + 180, 0
            ])

            geometry = new THREE.BufferGeometry()
            geometry.setAttribute('position', new THREE.BufferAttribute(vertex, 3))

            material = new THREE.LineBasicMaterial()
            material.setValues({ 'color': 0x666666 })

            group.add(new THREE.LineLoop(geometry, material))

            vertex = new Float32Array([
                300, 200, 0,
                350, 225, 0,
                300, 250, 0
            ])

            return group
        }

        // �ǫ�->�ⶥ��Alpha�ܤƦ��u
        this.drawPath = function (path) {
            let vertex = new Array()
            for (let i = 0; i < 256; i++)
                vertex.push(new THREE.Vector2(100 + i, 300 + path[i]))


            let geometry = new THREE.BufferGeometry().setFromPoints(vertex)

            return new THREE.Line(geometry)
        }

        this.getMinMax = function (logEnable, array) {
            if (array.length <= 0) {
                return {min:0, max:0}
            }

            let minValue = Math.log(array[0] + 1)
            let maxValue = Math.log(array[0] + 1)
            let value

            if (logEnable) {
                minValue = Math.log(minValue)
                maxValue = Math.log(maxValue)

                for (let i = 1; i < array.length; i++) {
                    value = Math.log(array[i] + 1)
                    if (value > maxValue) {
                        maxValue = value
                    }
                    else if (value < minValue) {
                        minValue = value
                    }
                }

            }

            else {
                for (let i = 1; i < array.length; i++) {
                    value = array[i] + 1
                    if (value > maxValue) {
                        maxValue = value
                    }
                    else if (value < minValue) {
                        minValue = value
                    }
                }
            }

            return { min: minValue, max: maxValue }

        }

        // �ǫפ����έp�Ϫ�
        this.drawLog = function (logEnable, histogram) {
            let material, geometry, vertex, group, process
            group = new THREE.Group()
            let max = this.getMinMax(logEnable, histogram).max
            let range = 100

            // draw line chart
            vertex = new Float32Array(256 * 3).fill(0)

            if (logEnable) {
                let ratio = range / (max + 1)
                for (let i = 0, y; i < 256; i++) {
                    y = 500 + (Math.log(histogram[i] + 1) + 1) * ratio - 1
                    vertex.set([
                        100 + i, y, 0
                    ], 3 * i)
                }
            }


            else {
                let ratio = range / (max + 1)

                for (let i = 0, y; i < 256; i++) {
                    y = 500 + (histogram[i] + 1) * ratio - 1
                    vertex.set([
                        100 + i, y, 0
                    ], 3 * i)
                }
            }
                        

            geometry = new THREE.BufferGeometry()
            geometry.setAttribute('position', new THREE.BufferAttribute(vertex, 3))

            material = new THREE.LineBasicMaterial()
            material.setValues({
                'color': 0x666666
            })

            group.add(new THREE.Line(geometry, material))

            // fill color into the chart
            vertex = new Float32Array(256 * 6).fill(0)
            if (logEnable) {
                let ratio = range / (max + 1)
                for (let i = 0, y; i < 256; i++) {
                    y = 500 + (Math.log(histogram[i] + 1) + 1) * ratio - 1
                    vertex.set([
                        100 + i, y, 0,
                        100 + i, 500, 0
                    ], 6 * i)
                }
            }
            else {
                let ratio = range / (max + 1)
                for (let i = 0, y; i < 256; i++) {
                    y = 500 + (histogram[i] + 1) * ratio - 1
                    vertex.set([
                        100 + i, y, 0,
                        100 + i, 500, 0
                    ], 6 * i)
                }
            }

            geometry = new THREE.BufferGeometry()
            geometry.setAttribute('position', new THREE.BufferAttribute(vertex, 3))

            material = new THREE.LineBasicMaterial()
            material.setValues({
                color: 0xffffff
            })

            group.add(new THREE.LineSegments(geometry, material))

            return group
        }

        // �ǫ�->RGBA�ⶥ�M�g
        this.drawColorSample = function (rgba) {
            let geometry, material, vertex, group

            group = new THREE.Group()

            geometry = new THREE.BufferGeometry()
            geometry.setAttribute('position', new THREE.BufferAttribute(new Float32Array(256 * 6), 3))
            geometry.setAttribute('color', new THREE.BufferAttribute(new Float32Array(256 * 6), 3))

            material = new THREE.LineBasicMaterial()
            material.setValues({ vertexColors: THREE.VertexColors })

            group.add(new THREE.LineSegments(geometry, material))

            vertex = new Float32Array([
                356, 480, 0,
                100, 480, 0,
                100, 500, 0,
                356, 500, 0
            ])

            geometry = new THREE.BufferGeometry()
            geometry.setAttribute('position', new THREE.BufferAttribute(vertex, 3))

            material = new THREE.LineBasicMaterial()
            material.setValues({ 'color': 0x666666 })

            group.add(new THREE.LineLoop(geometry, material))

            //console.log(group)
            this.updateColorSample(group, rgba)

            return group
        }

        this.updateColorSample = function(group, rgba) {
            let vertex = group.children[0].geometry.attributes.position.array
            let color = group.children[0].geometry.attributes.color.array

            for (let i = 0; i < 256; i++) {
                color.set([
                    rgba[0][i] * rgba[3][i], rgba[1][i] * rgba[3][i], rgba[2][i] * rgba[3][i],
                    rgba[0][i] * rgba[3][i], rgba[1][i] * rgba[3][i], rgba[2][i] * rgba[3][i]
                ], i * 6);

                vertex.set([
                    100 + i, 480, 0,
                    100 + i, 500, 0
                ], i * 6); 
            }
        }

        // �ǫ�->RGB�ⶥ�M�g
        this.drawRainbow = function (rgba) {

            const geometry = new THREE.BufferGeometry()
            
            geometry.setAttribute('position', new THREE.BufferAttribute( new Float32Array(256 * 6), 3))
            geometry.setAttribute('color', new THREE.BufferAttribute( new Float32Array(256 * 6), 3))

            const material = new THREE.LineBasicMaterial()
            material.setValues({ vertexColors: THREE.VertexColors })

            let mesh = new THREE.LineSegments(geometry, material)
            this.updateRanibow(mesh, rgba)
            return mesh
        }

        this.updateRanibow = function (mesh, rgba){
            let vertex = mesh.geometry.attributes.position.array
            let color = mesh.geometry.attributes.color.array

            for (let i = 0; i < 256; i++) {
                color.set([
                    rgba[0][i], rgba[1][i], rgba[2][i],
                    rgba[0][i], rgba[1][i], rgba[2][i]
                ], i * 6);

                vertex.set([
                    100 + i, 300, 0,
                    100 + i, 300 + 180, 0
                ], i * 6)
            }
        }
        
    }


}


class LittleTriangle {
    constructor() {
        this.x = 0
        this.hsv = new HSVType()
        this.hsv.set(0, 1, 1)
        this.marker = document.createElement('input')
        this.marker.type = 'color'

        this.compare = function (t) {
            return x == t.x
        }
    }
}

export { HSV, LittleTriangle }








