const maxSize = Math.pow(800, 3)
const bSize = Math.floor(maxSize / 8)

class BinaryPattern {
    constructor() {
        this._length = length
        this.data = new Uint8Array(bSize)
    }

    getBit = (index) => {
        let mask = 1 << index;
        return this.data & mask;
    }

    setBit = (index) => {
        let mask = 1 << index;
        this.data |= mask;
    }

    clearBit = (index) => {
        let mask = 1 << index;
        this.data &= ~mask;
    }

    changeBit = (index, value) => {
        let mask = 1 << index;
        this.data = this.data & ~mask | ((value << index) & mask);
    }

    clear = () => {
        this.data = 0;
    }

}

class BinaryArray {
    constructor(length) {
        this.data = new Uint8Array(Math.floor(length / 8));
        this._length = length;
        this._index = 0;
        this._group = 0;

        this._element = {
            get: () => {
                return (this.data[this._group] >> this._index) & 1;
            },
            set: (value) => {
                this.data[this._group] |= value << this._index;
            }
        };
    }

    getBit = (index) => {
        let ni = parseInt(index / 8)
        return (this.data[ni] >> (index % 8)) & 1
    }

    invert = (index) => {
        let value =  this.getBit(index) ^ 1;
        this.setValue(value, index);
    }

    setValue = (value, index) => {
        let ni = parseInt(index / 8);
        let i = (index % 8)
        let mask = 1 << i;
        
        this.data[ni] = this.data[ni] & ~mask | value << i;
    }

    setBit = (index) => {
        let ni = parseInt(index / 8);
        let mask = 1 << (index % 8);
        this.data[ni] |= mask;
    }

    clearBit = (index) => {
        let ni = parseInt(index / 8);
        let mask = 1 << (index % 8);
        this.data[ni] &= ~mask;
    }

    clear = () => {
        this.data.fill(0);
    }

    get(index) {
        console.log('t')
        this._index = index % 8;
        this._group = parseInt(index / 8);
        return this._element;
    }

    get length() {
        return this._length
    }

    fill = (v) => {
        this.data.fill(0)
    }

    slice = (startPos = 0, endPos = this.length) => {
        let result = new BinaryArray(this.length);
        result.data = this.data.slice();
        return result;
    }

    set = (newData) => {
        this.data.set(newData.data);
    }

    copyfrom = (array) => {
        if (!(array instanceof BinaryArray)) {
            return;
        }

        for (let i = 0; i < this.length && i < array.length; i++) {
            this.data[i] = array.data[i];
        }
    }
}

class Float16Array {
    constructor(length) {

        this._length = length
        this.data = new Float32Array(bSize)
    }

    getValue = (index) => {

        let ni = parseInt(index / 2)
        let t = this.data[ni] >> (index % 2)
        return t & 1
    }

    setValue = (v, index) => {

        let ni = parseInt(index / 2)
        let t = (v & 1) << (index % 2)

        this.data[ni] |= t
    }

    clear = () => {
        this.data.fill(0, 0, Math.ceil(this._length / 8))
    }

    get length() {
        return this._length
    }
}

class TypedArray3D {
    constructor(type = 'uint8', width = 0, height = 0, depth = 0, pSize = 0) {
        this._dimensions = [width, height, depth]
        this._pSize = pSize
        this._type = type

        switch (type) {
            case 'uint8':
                this.data = new Uint8Array(maxSize)
                break
            case 'uint8clamped':
                this.data = new Uint8ClampedArray(maxSize)
                break
            case 'uint16':
                this.data = new Uint16Array(maxSize)
                break
            case 'float32':
                this.data = new Float32Array(maxSize)
                break
            case 'float64':
                this.data = new Float64Array(maxSize)
                break
            default:
                this.data = new Uint8Array(maxSize)
                break
        }

        this.data.fill(0)

        let wh, w, full

        this.getValue3D = (wIndex, hIndex, dIndex) => {
//dIndex * pwidth * pheight + hIndex * pwidth + wIndex
            let index = dIndex * wh + hIndex * w + wIndex
            return this.data[index]
        }

        this.setValue3D = (value, wIndex, hIndex, dIndex) => {
            let index = dIndex * wh + hIndex * w + wIndex
            this.data[index] = value
        }

        this.getValue = (index) => {
            return this.data[index]
        }

        this.setValue = (index, value) => {
            this.data[index] = value
        }

        this.swap = (swapData) => {
            if (this._tmp == null) {
                this._tmp = this.data.subarray(0)
            } else {
                this._tmp.set(this.data)
            }

            this.data.set(swapData)
            swapData.set(this._tmp)
        }

        this.getValueWithPadding3D = (wIndex, hIndex, dIndex) => {
//(dIndex + pSize) * pwidth * pheight + hIndex * pwidth + wIndex
            let index = dIndex * wh + hIndex * w + wIndex + this.pSize * full
            return this.data[index]
        }

        this.setValueWithPadding3D = (value, wIndex, hIndex, dIndex) => {
            let index = dIndex * wh + hIndex * w + wIndex + this.pSize * full
            this.data[index] = value
        }

        this.needUpdate = () => {
            let p2 = this.pSize * 2
            this._pDimension =
                    [this._dimensions[0] + p2,
                        this._dimensions[1] + p2,
                        this._dimensions[2] + p2]

            wh = this._pDimension[0] * this._pDimension[1]
            w = this._pDimension[0]
            full = wh + w + 1
        }

        this.clear = () => {
            this.fill(0)
        }

        this.fill = (v = 0) => {
            this.data.fill(v, 0, this.length)
        }

        this.toArray = (result) => {
            if (result.length > this.data.length)
                result.set(this.data)
            else
                result.set(this.data, 0, result.length)
        }

        this.needUpdate()
    }

    set width(v) {
        this._dimensions[0] = v
    }

    set height(v) {
        this._dimensions[1] = v
    }

    set depth(v) {
        this._dimensions[2] = v
    }

    set pSize(v) {
        this._pSize = v
    }

    get width() {
        return this._dimensions[0]
    }

    get height() {
        return this._dimensions[1]
    }

    get depth() {
        return this._dimensions[2]
    }

    get pWidth() {
        return this._pDimensions[0]
    }

    get pHeight() {
        return this._pDimensions[1]
    }

    get pDepth() {
        return this._pDimensions[2]
    }

    get pSize() {
        return this._pSize
    }

    get length() {
        return this.pwidth * this.pheight * this.pdepth
    }

    transpose = () => {
        if (order.length < 3) {
            return
        }
    }

    foreach = (onload) => {
        if (!onload instanceof Function) {
            return
        }

        for (let i = 0; i < this.length; i++) {
            onload(this.data[i], i)
        }
    }
}

export { TypedArray3D, BinaryArray, BinaryPattern }
