import * as Opt from './ImageProcessTools.js'
import { Segment, ImageData } from '../model/Segment.js'
import {MaskImage} from "./MaskImage.js"

class SLController {
    constructor(segState) {
        //steps

        this.startStep = 0
        this.endStep = 1
        this.currentStep = 1
        this.length = 8

        this.tmpSegment = new Segment('tmp', [], null, false)

        this.mode = {
            NONE: -1,
            INITIAL: 0,
            addSeg: 1,
            removeSeg: 2,
            modifySeg: 3,
            changeIndex: 4
        }

        this.stack = new Array(this.length).fill(0).map(() => {
            return {
                action: this.mode.NONE,
                content: null,
                index: -1,
            }
        })

        this.onload = () => { }
        this.segState = segState
    }

    get undoEnable() {
        let limit = (this.startStep + 1) % this.length
        return limit != this.currentStep
    }

    get redoEnable() {
        return this.currentStep != this.endStep
    }

    setStackSeg(segment) {
        if (segment === null) {
            //do nothing
        }
        else if (this.stack[this.currentStep].content === null) {
            this.stack[this.currentStep].content = segment.clone();
        }
        else {
            this.stack[this.currentStep].content.copyfrom(segment);
        }
    }

    // �O�s���v�T��Segment
    // action:�ާ@����
    // index:�ާ@�ؼ�
    // segemnt:�ؼФ��e
    save(action) {

        this.currentStep = (this.currentStep + 1) % this.length
        this.endStep = this.currentStep

        if (this.endStep == this.startStep) {
            this.startStep = (this.startStep + 1) % this.length
        }

        this.stack[this.currentStep].action = action
        this.stack[this.currentStep].index = this.segState.focusedSegIndex

        this.setStackSeg(this.segState.focusedSegment)

        this.onload(this.undoEnable, this.redoEnable)
    }

    undo() {
        if (!this.undoEnable) {
            return
        }

        let stk = this.stack[this.currentStep]

        switch (stk.action) {
            case this.mode.addSeg:
                this.setStackSeg(this.segState.segments[stk.index + 1])

                this.segState.segments.splice(stk.index + 1, 1)
                this.segState.focusedSegIndex = stk.index

                break
            case this.mode.removeSeg:

                this.segState.segments.splice(stk.index, 0, stk.content.clone())
                this.segState.focusedSegIndex = stk.index
                break
            case this.mode.modifySeg:

                this.segState.focusedSegIndex = stk.index
                this.tmpSegment.copyfrom(this.segState.focusedSegment)

                this.segState.focusedSegment.copyfrom(stk.content)
                this.setStackSeg(this.tmpSegment)
                break
        }

        this.currentStep = (this.currentStep + this.length - 1) % this.length
        this.onload(this.undoEnable, this.redoEnable)
    }

    redo() {
        if (!this.redoEnable) {
            return
        }

        this.currentStep = (this.currentStep + 1) % this.length

        let stk = this.stack[this.currentStep]

        switch (stk.action) {
            case this.mode.addSeg:
                this.segState.segments.splice(stk.index + 1, 0, stk.content.clone())
                this.segState.focusedSegIndex = stk.index + 1
                break
            case this.mode.removeSeg:
                this.segState.segments.splice(stk.index, 1)
                this.segState.focusedSegIndex = stk.index - 1
                break
            case this.mode.modifySeg:
                this.segState.focusedSegIndex = stk.index
                this.tmpSegment.copyfrom(this.segState.focusedSegment)
                this.segState.focusedSegment.copyfrom(stk.content)
                this.setStackSeg(this.tmpSegment)
                break
        }

        this.onload(this.undoEnable, this.redoEnable)
    }
}


export class SegmentManager {

    constructor(segState) {
        this.maskImages = []
        this.events = []

        for (let i = 0; i < 3; i++) {
            this.maskImages.push(new MaskImage(segState, i))
        }

        this.mode = {
            NONE: 0,
            BRUSH: 1,
            THRESHOLD: 2,
            MORPH: 3,
            LOGIC: 4,
            REGIONGROW: 5,
            SCISSOR: 6,
            ML: 7,
            SIZEBASED: 8,
            TRANSFER: 9
        }

        this.state = this.mode.NONE
        this.segState = segState

        for (let i = 0; i < this.maskImages.length; i++) {
            this.maskImages[i].segState = this.segState
        }

        this.slc = new SLController(this.segState)//存檔/讀檔控制

        this.cropTools = new Opt.CropTools(this.segState, this.maskImages)
        this.cropTools.enable = true
        this.cropTools.onload = () => {
            this.notify()
        }

        this.listControlTools = new Opt.ListControlTools(this.segState, this.maskImages)
        this.listControlTools.enable = true
        this.listControlTools.onstart = (result) => {
            if (result == true) {
                this.slc.save(this.slc.mode.addSeg)
            }
            else if (result == false) {
                this.slc.save(this.slc.mode.removeSeg)
            }
            
        }
        this.listControlTools.onload = () => {
            this.notify()
        }

        this.logicTools = new Opt.LogicTools(this.segState)
        this.logicTools.onstart = () => {
            this.slc.save(this.slc.mode.modifySeg)
        }
        this.logicTools.onload = () => {
            this.notify()
        }

        this.brushTools = new Opt.BrushTools(this.segState, this.maskImages)
        this.brushTools.onstart = () => {
            this.slc.save(this.slc.mode.modifySeg)
        }
        this.brushTools.onload = () => {
            this.notify()
        }

        this.filterTools = new Opt.FilterTools(this.segState)
        this.filterTools.onstart = () => {
            this.slc.save(this.slc.mode.modifySeg)
        }
        this.filterTools.onload = () => {
            this.notify()
        }

        this.regionGrowing = new Opt.GrowingTools(this.segState, this.maskImages)
        this.regionGrowing.onstart = () => {
            this.slc.save(this.slc.mode.modifySeg)
        }
        this.regionGrowing.onload = () => {
            this.notify()
        }

        this.thresholdTools = new Opt.ThresholdTools(this.segState)
        this.thresholdTools.onstart = () => {
            this.slc.save(this.slc.mode.modifySeg)
        }
        this.thresholdTools.onload = () => {
            this.notify()
        }

        let domElement = document.getElementById('transfer_layout')
        this.transferTools = new Opt.TransferTools(this.segState, domElement)
        this.transferTools.onstart = () => {
            this.slc.save(this.slc.mode.modifySeg)
        }
        this.transferTools.onload = () => {
            this.notify()
        }

        domElement = document.getElementById('tool_sizebased_histogram')
        this.sizeBasedTools = new Opt.SizeBasedTools(this.segState, domElement)
        this.sizeBasedTools.onstart = () => {
            this.slc.save(this.slc.mode.modifySeg)
            console.log('ttt')
        }
        this.sizeBasedTools.onload = () => {
            this.notify()
        }

        domElement = document.getElementById('dcmViewer')
        this.scissorTools = new Opt.ScissorTools(this.segState, domElement)
        this.scissorTools.onstart = () => {
            this.slc.save(this.slc.mode.modifySeg)
        }
        this.scissorTools.onload = () => {
            this.notify()
        }
        
    }

    disableAll() {
        this.logicTools.enable = false
        this.brushTools.enable = false
        this.filterTools.enable = false
        this.regionGrowing.enable = false
        this.thresholdTools.enable = false
        this.scissorTools.enable = false
        this.transferTools.enable = false
        this.sizeBasedTools.enable = false
    }

    enable() {
        this.setManagerTools(this.state)
    }

    setBaseSegment(data, uvdDims, spacing) {
        //console.log(dataBuffer)

        this.segState.baseSegment = new ImageData('base', uvdDims, data, spacing);

        this.segState.spacing = spacing;
        //this.segState.byte = byte

        this.maskImages[axisUV].setCanvasSize(uvdDims[0], uvdDims[1]);
        this.maskImages[axisUD].setCanvasSize(uvdDims[0], uvdDims[2]);
        this.maskImages[axisVD].setCanvasSize(uvdDims[1], uvdDims[2]);
        //this.slc.save(this.slc.mode.INITIAL, null, this.segState.base)

        this.sizeBasedTools.calculateFrequency();
    }

    setManagerTools(option) {
        this.disableAll()

        switch (option) {
            case this.mode.BRUSH:
                this.state = option
                this.brushTools.enable = true
                break
            case this.mode.THRESHOLD:
                this.state = option
                this.thresholdTools.enable = true
                break
            case this.mode.MORPH:
                this.state = option
                this.filterTools.enable = true
                break
            case this.mode.REGIONGROW:
                this.state = option
                this.regionGrowing.enable = true
                break
            case this.mode.LOGIC:
                this.state = option
                this.logicTools.enable = true
                break
            case this.mode.NONE:
                this.state = option
                break
            case this.mode.SCISSOR:
                this.state = option
                this.scissorTools.enable = true
                break
            case this.mode.ML:
                this.state = option
                //this.scissorTools.enable = true
                break
            case this.mode.SIZEBASED:
                this.state = option
                this.sizeBasedTools.enable = true
                break
            case this.mode.TRANSFER:
                this.state = option
                this.transferTools.enable = true
                break
            default:
                this.state = this.mode.NONE
        }
    }

    getFocusedLayer(axis) {
        let segment = this.segState.focusedSegment
        let image = this.maskImages[axis]
        let index = image.index

        if (segment == null || index == -1) {
            return null
        }

        let data = image.getLayerImage(0)

        return segment.binaryLoader(axis, index, data)
    }

    setFocusedLayer(axis, index = this.maskImages[axis].index) {

        if (this.segState.baseSegment == null) {
            return
        }

        let image = this.maskImages[axis]
        this.maskImages[axis].index = index

        //取得canvas data
        let imgdata = image.getBaseImage()

        //從3D中取出指定層數的2D影像，以canvas data保存
        this.segState.baseSegment.imgLoader(axis, index, imgdata)
        //this.segState.originSegment.addBuffer(axis, index, imgdata)

        if (this.segState.baseSegment.used) {
            for (let i = 0, j = 0; i < data.length; i += 4, j++) {
                data[i + 3] = this.segState.baseSegment.alpha[j]
            }
        }

        //將canvas data存回canvas中
        image.setBaseImage(imgdata)



        while (image.layers.length > this.segState.segments.length) {
            image.removeLayer()
        }

        while (image.layers.length < this.segState.segments.length) {
            image.addLayer()
        }

        let color, pixelData, segment
        for (let i = 0; i < this.segState.segments.length; i++) {
            segment = this.segState.segments[i]
            color = segment.color
            pixelData = image.getLayerImage(i)
            segment.binaryLoader(axis, index, pixelData)
            image.setLayerImage(i, pixelData, color)
        }

        image.update()
    }

    addNotifyEvent = (event) => {
        this.events.push(event)
    }

    notify() {
        for (let index in this.maskImages) {
            this.setFocusedLayer(index)
        }

        for (let i = 0; i < this.events.length; i++) {
            this.events[i]()
        }
    }
}