export class MaskImage {

    constructor(segState, orientaion = 0) {
        this.exposure = 1
        this.contrast = 1
        this.index = -1
        this.orientation = orientaion

        this.domElements = {
            background: {},
            segment: {},
            controller: {}
        }

        for (let key in this.domElements) {

            this.domElements[key].canvas = document.createElement('canvas')
            this.domElements[key].context = this.domElements[key].canvas.getContext('2d')
            this.domElements[key].palatte = ''
            this.domElements[key].process = () =>{
                this.domElements[key].context.putImageData(this.domElements[key].palatte, 0, 0)
            }
            this.domElements[key].setSize = (width, height) =>{
                this.domElements[key].canvas.width = width
                this.domElements[key].canvas.height = height
                this.domElements[key].palatte = this.domElements[key].context.getImageData(0, 0, width, height)
            }
        }

        this.layers = []
        this.segState = segState

        this.size = []
        this.compressedSize = []

        this.setCanvasStyle= (width, height) => {
            for (let index in this.domElements) {
                this.domElements[index].canvas.style.width = `${width}px`
                this.domElements[index].canvas.style.height = `${height}px`
            }
        }

        this.setCanvasSize = (width, height, sync = true) => {

            this.size = [width, height]


            for (let index in this.domElements) {
                this.domElements[index].setSize(width, height)
            }
        }

        this.setBaseImage = (data) => {
            
            for (let i = 0; i < data.length; i++) {
                this.domElements.background.palatte.data[i] = parseInt(data[i])
            }

            //console.log(buffer,this.palatte.data)
            this.domElements.background.process()
        }

        this.setLayerImage = (index, pixelData, color) => {
            //console.log(buffer)
            if (this.layers.length <= index || index < 0) {
                console.error('mask image: index out of range')
                return
            }

            let layer = this.layers[index]
            let palatte = layer.palatte
            let ctx = layer.context

            palatte.data.set(pixelData)
            ctx.putImageData(palatte, 0, 0)
            //console.log(color)
            if (color != null) {
                ctx.save()
                ctx.globalCompositeOperation = 'source-in'
                ctx.fillStyle = color
                ctx.globalAlpha = 0.5
                ctx.beginPath()
                ctx.rect(0, 0, layer.width, layer.height)
                ctx.fill()
                ctx.restore()
            }

        }

        this.addLayer = () => {
            let layer = document.createElement('canvas')
            layer.width = this.size[0]
            layer.height = this.size[1]
            let ctx = layer.getContext('2d')
            this.layers.push(
                {
                    canvas: layer,
                    context: ctx,
                    width: this.size[0],
                    height: this.size[1],
                    palatte: ctx.getImageData(0, 0, this.size[0], this.size[1])
                }
            )
        }

        this.removeLayer = (index) => {
            this.layers.splice(index, 1)
        }

        this.clear = () => {
            let ctx = this.domElements.segment.context
            ctx.clearRect(0, 0, this.size[0], this.size[1])
        }

        this.update = () => {
            this.clear()
            let fIndex = this.segState.focusedSegIndex

            let ctx = this.domElements.segment.context

            //ctx.filter = 'brightness(' + this.exposure + ') contrast(' + this.contrast +')'
            //ctx.drawImage(this.domElements.background, 0, 0)
            ctx.filter = 'none'
            //ctx.globalCompositeOperation = 'lighter'

            for (let i = 0; i < this.segState.segments.length; i++) {
                if (this.segState.segments[i].visible && i != fIndex)
                    ctx.drawImage(this.layers[i].canvas, 0, 0)
            }

            if (fIndex != -1 && this.segState.segments[fIndex].visible)
                ctx.drawImage(this.layers[fIndex].canvas, 0, 0)

            //ctx.globalCompositeOperation = 'source-over'
        }


        this.updateBorder = (points) => {
            let ctx = this.domElements.controller.context

            ctx.save()
            ctx.beginPath()
            for (let i = 0; i < points.length; i++) {
                if (i == 0) {
                    ctx.moveTo(points[i].x, points[i].y)
                }
                else {
                    ctx.lineTo(points[i].x, points[i].y)
                }
            }
            ctx.closePath()
            ctx.strokeStyle = '#FF0000'
            ctx.lineWidth = 3
            ctx.lineCap = 'round'
            ctx.stroke()
            ctx.restore()

            ctx.save()
            ctx.fillStyle = '#FF0000'
            for (let i = 0; i < points.length; i++) {
                ctx.beginPath()
                ctx.arc(points[i].x, points[i].y, 6, 0, Math.PI * 2)
                ctx.fill()
            }
            ctx.restore()
        }

        this.setControllerTrack = (index, pos, radius, color) => {

            let ctx = this.domElements.controller.context

            ctx.save()
            ctx.beginPath()
            //ctx.strokeStyle = color
            ctx.fillStyle = color
            ctx.lineWidth = 6
            ctx.arc(pos[0], pos[1], parseInt(radius) + 3, 0, Math.PI * 2)
            //ctx.stroke()
            ctx.fill()
            ctx.restore()
        }

        this.clearControllerTrack = () => {
            let ctx = this.domElements.controller.context
            ctx.clearRect(0, 0, this.size[0], this.size[1])
        }

        this.setMaskTrack = (index, pos, radius, color, erase = false) => {
            if (index >= this.layers.length || index < 0) {
                return
            }

            let ctx = this.layers[index].context

            ctx.save()
            ctx.globalCompositeOperation = 'source-over'
            if (erase) {
                ctx.globalCompositeOperation = 'destination-out'
            }

            ctx.beginPath()
            ctx.fillStyle = color
            ctx.arc(pos[0], pos[1], radius, 0, Math.PI * 2, 1)
            ctx.fill()
            ctx.restore()
        }

        this.getLayerImage = (index) => {
            let ctx = this.layers[index].context
            return ctx.getImageData(0, 0, this.size[0], this.size[1]).data
        }

        this.getBaseImage = () => {
            let ctx = this.domElements.background.context
            return ctx.getImageData(0, 0, this.size[0], this.size[1]).data
        }

        this.toImgURL = () => {
            return this.background.toDataURL()
        }

        this.setCanvasSize(this.background.width, this.background.height)
    }

    get background() {
        return this.domElements.background.canvas
    }

    get controller() {
        return this.domElements.controller.canvas
    }

    get segment() {
        return this.domElements.segment.canvas
    }

    get thickness() {
        let spacing = this.segState.spacing

        switch (this.orientation) {
            case 0:
                return [spacing[0], spacing[1]]
            case 1:
                return [spacing[0], spacing[2]]
            case 2:
                return [spacing[1], spacing[2]]
        }

    }
}