import {ImageProcess} from "../controller/ImageProcessTools.js"
import {BinaryArray} from "./ExtendedArray.js"

class SegState {
    constructor() {
        // �ϥ�A/B state����ثe�v����ƬO�_�����L
        // first time:state A
        // second time:state B
        // third time:return to state A
        // ......

        this.mode = { A: 0, B: 1 }
        this.images = {
            origin: null,
            base: null,
            clahe: null
        }

        this.imgRenderType = -1
        this.mask = null
        this.order = -1
        this.segments = []
        this.fIndex = -1// 當前被選擇的樣板
        this.state = this.mode.A
        this.spacing = [1, 1, 1]// 像素間隔
        this.colormap = new Float32Array(256 * 4)
        this.byte = 8 // 資料的位元數
        this.imgRenderType = 0;

        for (let i = 0; i < 256; i++) {
            this.colormap[i * 4] = 1
            this.colormap[i * 4 + 1] = 1
            this.colormap[i * 4 + 2] = 1
            this.colormap[i * 4 + 3] = i / 255
        }
        
        let imageProcess = new ImageProcess()
        
        this.generate = () => {
            this.baseDataReset()
            imageProcess.postprocess(this.images.base.data, this.images.base.dims, this.imgRenderType)
        }

        this.baseDataReset = () => {
            if (this.images.base === null || this.images.origin === null || this.images.base.data.length !== this.images.origin.data.length) {
                console.error('Internal data missing!');
            }

            pushData(this.images.origin.data, this.images.base.data);
        }

        this.clone = () =>{
            let cl = new SegState()

            cl.order = this.order
            cl.fIndex = this.fIndex

            this.segments.forEach((segment) => {
                cl.segments.push(segment.clone())
            })

            return cl
        }

        this.copyfrom = (state) => {

            this.order = state.order
            this.segments = []
            this.fIndex = state.fIndex

            state.segments.forEach((segment) => {
                this.segments.push(segment.clone())
            })

        }
    }

    get focusedSegIndex() {
        return this.fIndex
    }

    set focusedSegIndex(x) {
        this.fIndex = x
    }

    get focusedSegment() {
        if (this.fIndex >= 0 && this.fIndex < this.segments.length) {
            return this.segments[this.fIndex]
        }
        return null
    }

    set focusedSegment(x) {
        this.segments[this.fIndex] = x
    }

    set baseSegment(imgData) {
        this.images.origin = imgData;
        this.images.base = imgData.clone();
    }

    get originSegment() {
        return this.images.origin;
    };
 
    get baseSegment() {
        return this.images.base;
    };
    
    
}

class ImageData {
    /**
     * 
     * @param {any} name used for segment identity
     * @param {any} dims 3 dimentions: x, y, z
     * @param {any} color used for segment identity
     */
    constructor(name = 'default', dims = [0, 0, 0], data = null, spacing = [1, 1, 1]) {
        this.dims = [...dims];
        this.name = name;
        this.step = 1;

        //壓縮記憶體使用量

        this.data = data;
        this.sizeEnable = false;
        this.spacing = spacing;

        this.clear = () => {
            if(this.data !== null)
            this.data.fill(0)
        }

        /**
         * 影像資料採用float保存(0~1)
         * 取出時需要映射至0~255才能給canvas使用
         */
        

        this.imgWriter = function (axis, index, result) {
            let uvdDims = this.dims
            let i, j, k, step, count = 0
            let scalar = writerScalar(this.data)

            if(scalar == -1){
                return
            }

            //load top 2 bottom
            //console.log(dataBuffer)
            if (axis == axisUV) {
                step = uvdDims[1] * uvdDims[0]
                for (i = index * step; i < index * step + step; i++) {
                    this.data[i] = result[4 * count + 3]*scalar
                    count++
                }
            }

            //load left 2 right
            else if (axis == axisUD) {
                step = uvdDims[1] * uvdDims[0]
                for (j = index * uvdDims[0], i = 0; j < this.data.length; j += step, i += uvdDims[0]) {
                    for (k = j; k < j + uvdDims[0]; k++) {
                        this.data[k] = result[4 * count + 3]*scalar
                        count++
                    }
                    //dataBuffer.set(result.subarray(i, i + uvdDims[0]), j, uvdDims[0])
                }
            }

            //load front 2 back
            else {
                for (j = uvdDims[0] - index - 1, i = 0; j < this.data.length; j += uvdDims[0], i++) {
                    this.data[j] = result[4 * count + 3]*scalar
                    count++
                }
            }
        }

        this.imgLoader = function (axis, index, result) {
            let uvdDims = this.dims
 
            //pixelEnhense(dataBuffer)
            let i, j, k, step, startPos
            let scalar = loaderScalar(this.data)

            result.fill(255)

            if (axis == axisUV) {
                step = uvdDims[1] * uvdDims[0]
                startPos = index * step

                let count = 3
                for (i = startPos; i < startPos + step; i++) {
                    result[count] = this.data[i] * scalar
                    count += 4
                }
            }

            else if (axis == axisUD) {
                let jstep = uvdDims[1] * uvdDims[0]
                let count = 3
                for (j = index * uvdDims[0]; j < this.data.length; j += jstep) {

                    for (k = j; k < j + uvdDims[0]; k++) {
                        result[count] = this.data[k] * scalar
                        count += 4
                    }
                }
            }

            else {
                let count = 3
                for (j = uvdDims[0] - index - 1; j < this.data.length; j += uvdDims[0]) {
                    result[count] = this.data[j] * scalar
                    count += 4
                }
            }

            //console.log(result)
        }

        this.bright = (v) => {
            let iv = 1 - v
            for (let i = 0; i < this.data.length; i++) {
                this.data[i] = this.data[i] * v + iv
            }
        }

        this.addBuffer = (axis, index, result) => {
            //pixelEnhense(dataBuffer)
            let i, j, k, step, startPos, p, q
            //load top 2 bottom
            let uvdDims = this.dims
            let dataBuffer = this.data
            let ratio = 0.2

            if (axis == axisUV) {
                step = uvdDims[1] * uvdDims[0]
                startPos = index * step

                let count = 3//alpha通道位於陣列的index為3的倍數
                for (i = startPos; i < startPos + step; i++) {
                    p = result[count]
                    q = dataBuffer[i] * 255
                    result[count] = p + Math.abs(q - p) * ratio
                    count += 4
                }
                //buffer.set(dataBuffer.subarray(startPos, (startPos + step)))
            }

            //load left 2 right
            else if (axis == axisUD) {
                let jstep = uvdDims[1] * uvdDims[0]
                let count = 3

                for (j = index * uvdDims[0]; j < dataBuffer.length; j += jstep) {

                    for (k = j; k < j + uvdDims[0]; k++) {
                        p = result[count]
                        q = dataBuffer[k] * 255
                        result[count] = p + Math.abs(q - p) * ratio
                        count += 4
                    }
                    //buffer.set(dataBuffer.subarray(j, j + uvdDims[0]), startPos)
                }
            }

            //load front 2 back
            else {

                let count = 3
                for (j = uvdDims[0] - index - 1; j < dataBuffer.length; j += uvdDims[0]) {
                    p = result[count]
                    q = dataBuffer[j] * 255
                    result[count] = p + Math.abs(q - p) * ratio
                    count += 4
                }
            }
        }
        
        this.clone = (deep) => {
            return new ImageData(this.name, this.dims, this.data.slice());
        };

        this.copyfrom = (imageData) => {
            this.dims = [...imageData.dims];

            this.name = imageData.name;
            this.step = imageData.step;

            if (this.data.length === imageData.data.length) {
                this.data.set(imageData.data);
            }
            
            this.data = imageData.data.slice();

        };

        /**
        this.multiply = (axis, index, result) => {
            //pixelEnhense(dataBuffer)
            let i, j, k, step, startPos
            //load top 2 bottom
            let uvdDims = this.dims
            let dataBuffer = this.data

            result.fill(255)

            if (axis == axisUV) {
                step = uvdDims[1] * uvdDims[0]
                startPos = index * step

                let count = 0
                for (i = startPos; i < startPos + step; i++) {
                    result[count + 3] *= dataBuffer[i]
                    count += 4
                }
                //buffer.set(dataBuffer.subarray(startPos, (startPos + step)))
            }

            //load left 2 right
            else if (axis == axisUD) {
                step = uvdDims[1] * uvdDims[0]


                for (startPos = 0, j = index * uvdDims[0]; j < dataBuffer.length; j += step, startPos += uvdDims[0] * 4) {
                    let count = 0
                    for (k = j; k < j + uvdDims[0]; k++) {
                        result[count + startPos + 3] *= dataBuffer[k]
                        count += 4
                    }

                    //buffer.set(dataBuffer.subarray(j, j + uvdDims[0]), startPos)
                }
            }

            //load front 2 back
            else {

                let count = 0
                for (j = uvdDims[0] - index - 1; j < dataBuffer.length; j += uvdDims[0]) {
                    result[count + 3] *= dataBuffer[j]
                    count += 4
                }
            }
        }
         */

        this.applyMask = (mask) => {
            if (this.data.length != mask.length)
                return

            for (let i = 0; i < this.data.length; i++) {
                if (mask[i] != 1) {
                    this.data[i] = 0
                }
            }
        }

        this.generateThumbnail = () => {
            let scalar = loaderScalar(this.data)
            let size = [
                Math.round(this.dims[0] / 2),
                Math.round(this.dims[1] / 2),
                Math.round(this.dims[2] / 2),
            ]

            let total = size[0] * size[1] * size[2]

            if(this.thumbnail==null || this.thumbnail.length!=total)
                this.thumbnail = new Uint8ClampedArray(total)

            this.thumbnail.fill(0)
            
            let distep, djstep, tistep, tjstep
            for(let di = 0, ti = 0;di<this.dims[2];di+=2, ti++){

                distep = di * this.dims[0] * this.dims[1]
                tistep = ti * size[0] * size[1]

                for(let dj = 0, tj = 0;dj<this.dims[1];dj+=2, tj++){

                    djstep = dj * this.dims[0]
                    tjstep = tj * size[0]

                    for(let dk = 0, tk = 0;dk<this.dims[0];dk+=2, tk++){
                        this.thumbnail[tistep + tjstep + tk] = this.data[distep + djstep + dk] * scalar
                    }
                }
            }
        }
    }

    get layer() {
        return this.dims[2]
    }


}

class Segment {
    /**
     * 
     * @param {any} name used for segment identity
     * @param {any} dims 3 dimentions: x, y, z
     * @param {any} color used for segment identity
     */
    constructor(name = 'default', dims = [0, 0, 0], color = '000000', visible = true) {
        this.dims = [...dims]
        this.name = name
        this.color = color
        this.step = 1
        this.thumbnail = null
        this.thumbnailDims = new Array(3)

        //壓縮記憶體使用量
        this.data = new BinaryArray(dims[0] * dims[1] * dims[2])
        this.data.fill(0)

        this.visible = visible
        this.sizeEnable = false
        //this.sizeData = new Float32Array(dims[0] * dims[1] * dims[2]).fill(0)

        this.clear = () => {
            this.data.fill(0)
        }

        /**
         * 影像資料採用float保存(0~1)
         * 取出時需要映射至0~255才能給canvas使用
         */
        this.binaryWriter = function (axis, index, result) {
            let uvdDims = this.dims
            let data = this.data
            let i, j, k, step, count = 0

            //load top 2 bottom
            //console.log(dataBuffer)
            if (axis == axisUV) {
                step = uvdDims[1] * uvdDims[0]
                for (i = index * step; i < index * step + step; i++) {
                    result[4 * count + 3] > 0 ? data.setBit(i) : data.clearBit(i)
                    count++
                }
            }

            //load left 2 right
            else if (axis == axisUD) {
                step = uvdDims[1] * uvdDims[0]
                for (j = index * uvdDims[0], i = 0; j < this.length; j += step, i += uvdDims[0]) {
                    for (k = j; k < j + uvdDims[0]; k++) {
                        result[4 * count + 3] > 0 ? data.setBit(k) : data.clearBit(k)
                        count++
                    }
                    //dataBuffer.set(result.subarray(i, i + uvdDims[0]), j, uvdDims[0])
                }
            }

            //load front 2 back
            else {
                for (j = uvdDims[0] - index - 1, i = 0; j < this.length; j += uvdDims[0], i++) {
                    result[4 * count + 3] > 0 ? data.setBit(j) : data.clearBit(j)
                    count++
                }
            }
        }

        this.binaryLoader = function (axis, index, result) {
            let uvdDims = this.dims
            let data = this.data
            //pixelEnhense(dataBuffer)
            let i, j, k, step, startPos

            result.fill(255)
            if (axis == axisUV) {
                step = uvdDims[1] * uvdDims[0]
                startPos = index * step

                let count = 3
                for (i = startPos; i < startPos + step; i++) {
                    result[count] = data.getBit(i) * 255
                    count += 4
                }
            }

            else if (axis == axisUD) {
                let jstep = uvdDims[1] * uvdDims[0]
                let count = 3
                for (j = index * uvdDims[0]; j < this.length; j += jstep) {

                    for (k = j; k < j + uvdDims[0]; k++) {
                        result[count] = data.getBit(k) * 255
                        count += 4
                    }
                }
            }

            else {
                let count = 3
                for (j = uvdDims[0] - index - 1; j < this.length; j += uvdDims[0]) {
                    result[count] = data.getBit(j) * 255
                    count += 4
                }
            }
        }

        
        this.clone = (deep) => {
            let tmpSeg = new Segment(this.name, this.dims, this.color, this.visible);
            pushData(this.data.data, tmpSeg.data.data)
            return tmpSeg;
        }

        this.copyfrom = (segment) => {
            this.dims = [...segment.dims];

            this.name = segment.name;
            this.color = segment.color;
            this.step = segment.step;
            this.visible = segment.visible;
            
            if (this.data.length != segment.data.length) {
                this.data = new BinaryArray(segment.data.length)
            }
            
            pushData(segment.data.data, this.data.data)
        };

        this.generateThumbnail = () => {
            let size = [
                Math.round(this.dims[0] / 2),
                Math.round(this.dims[1] / 2),
                Math.round(this.dims[2] / 2),
            ]

            size.push(size[0] * size[1] * size[2])

            if(this.thumbnail==null || this.thumbnail.length!=size[3])
                this.thumbnail = new Uint8ClampedArray(size[3])

            this.thumbnail.fill(0)

            let distep, djstep, tistep, tjstep
            for(let di = 0, ti = 0;di<this.dims[2];di+=2, ti++){
                
                distep = di * this.dims[0] * this.dims[1]
                tistep = ti * size[0] * size[1]
                
                for(let dj = 0, tj = 0;dj<this.dims[1];dj+=2, tj++){
                    
                    djstep = dj * this.dims[0]
                    tjstep = tj * size[0]
                    
                    for(let dk = 0, tk = 0;dk<this.dims[0];dk+=2, tk++){
                        this.thumbnail[tistep + tjstep + tk] = this.data.getBit(distep + djstep + dk) * 255
                    }
                }
            }
            
        }

        

    }

    get layer() {
        return this.dims[2];
    }

    get length(){
        return this.dims[0] * this.dims[1] * this.dims[2];
    }

}

export { Segment, SegState, ImageData }