class ScissorTools extends SegTools {
    constructor(segState, domElement) {
        super(segState)

        this.domElement = domElement
        this.canvas = document.createElement('canvas')
        this.canvas.width = domElement.clientWidth
        this.canvas.height = domElement.clientHeight
        this.canvas.classList.add('paintingViewer')
        this.context = this.canvas.getContext('2d')

        this.domElement.appendChild(this.canvas)
        this.__enable = false
        let state = 0
        let color = 'rgba(255,0,0,0.2)'
        let penPos = [0, 0]
        let radius = 5
        //console.log(this)
        this.canvas.addEventListener('mousedown', () => {
            if (!this.enable) {
                return
            }

            state = 1
        })

        this.canvas.addEventListener('mousemove', (evt) => {

            if (!this.enable) {
                return
            }

            if (state == 1) {
                penPos = getMousePos(this.canvas, evt)
                this.context.fillStyle = color
                this.context.beginPath()
                this.context.arc(penPos[0], penPos[1], radius, 0, Math.PI * 2, 1)
                this.context.fill()
            }
        })

        window.addEventListener('mouseup', () => {
            if (!this.enable) {
                return
            }

            this.context.clearRect(0, 0, this.canvas.width, this.canvas.height)
            state = 0
        })

        window.addEventListener('resize', () => {
            this.canvas.width = domElement.clientWidth
            this.canvas.height = domElement.clientHeight
        })
    }



    set enable(option) {
        if (this.canvas == null) {
            return
        }

        this.__enable = option
        if (option) {
            this.canvas.style.display = 'block'
        }
        else {
            this.canvas.style.display = 'none'
        }
    }

    get enable() {
        return this.__enable
    }

    process(camera, img) {
        if (!this.enable) {
            return
        }

        let dims = this.segState.baseSegment.dims
        let ss = new Scissor(dims)
        let mask = ss.process(camera, img)

        let seg = this.getFocusedSegment()

        if (seg == null) {
            return
        }

        let segData = seg.data

        this.onstart()

        Logic.union(segData, mask)

        this.onload()
    }
}

class ThresholdTools extends SegTools {
    constructor(segState) {
        super(segState)
        this.mode = {
            MANUAL: 0,
            AUTO: 1
        }
        this.state = 0
        this.l_limit = 0
        this.r_limit = 0

        let otsu = (histogram, total) => {
            
            let sum = 0;
            for (let i = 1; i < histogram.length; ++i)
                sum += i * histogram[i];
            let sumB = 0;
            let wB = 0;
            let wF = 0;
            let mB;
            let mF;
            let max = 0;
            let between = 0;
            let threshold1 = 0;
            let threshold2 = 0;
            for (let i = 0; i < histogram.length; ++i) {
                wB += histogram[i];
                if (wB == 0)
                    continue;
                wF = total - wB;
                if (wF == 0)
                    break;
                sumB += i * histogram[i];
                mB = sumB / wB;
                mF = (sum - sumB) / wF;
                between = wB * wF * (mB - mF) * (mB - mF);
                if (between >= max) {
                    threshold1 = i;
                    if (between > max) {
                        threshold2 = i;
                    }
                    max = between;
                }

            }

            return (threshold1 + threshold2) / 2.0;
        }

        this.getAutoValue = () => {
            let baseData = this.segState.baseSegment.data
            let maxSize = getDataRange(baseData)

            let histogram = new Array(maxSize).fill(0)
            let cvtColor = 0
            let total = 0

            for (let i = 0; i < baseData.length; i++) {
                cvtColor = parseInt(baseData[i])
                histogram[cvtColor]++
                total++
            }

            return otsu(histogram, total) / maxSize
        }

        this.process = () => {
            //console.log('ttt')
            if (!this.enable) {
                return
            }
            //console.log('ttk')
            let index = this.segState.focusedSegIndex
            let segment = this.segState.focusedSegment

            if (index == -1 || segment == null) {

                return
            }

            let baseData = this.segState.baseSegment.data
            let segData = segment.data
            let scalar = getDataRange(baseData)
            let maskData = null

            this.onstart()

            switch (this.state) {
                case this.mode.MANUAL:
                    for (let i = 0; i < baseData.length; i++) {
                        segData.setValue(0, i);
                        if (baseData[i] >= this.l_limit * scalar && baseData[i] <= this.r_limit * scalar) {
                            segData.setValue(1, i);
                            //console.log('t')
                        }
                    }
                    break
                case this.mode.AUTO:

                    let thres = this.getAutoValue()
                    let maxSize = getDataRange(baseData)

                    for (let i = 0; i < baseData.length; i++) {
                        segData.setValue(0, i);
                        if (baseData[i] >= thres * maxSize) {
                            segData.setValue(1, i);
                            
                        }
                    }
                    break
                default:
                    break
            }




            this.onload()
        }
    }


}

class ListControlTools extends SegTools {
    constructor(segState, maskImages) {
        super(segState, true)
        this.mode = {
            CREATE: 0,
            REMOVE: 1,
            MOVEUP: 2,
            MOVEDOWN: 3,
            MOVETOP: 4,
            MOVEBOTTOM: 5
        }
        this.colorList = ['#FF0000', '#00FF00', '#0000FF', '#F0F0F0', '#0F0F0F']

        let create = () => {
            if (this.segState.baseSegment == null) {
                alert('image data not load!')
                return false
            }

            this.onstart(true)

            let color = this.colorList[this.segState.segments.length % this.colorList.length]
            let dims = [...this.segState.baseSegment.dims]
            let newSeg = new Segment('Segment', dims, color,true, true)
            this.segState.segments.splice(this.segState.focusedSegIndex + 1, 0, newSeg)
            this.segState.focusedSegIndex++

            this.onload()

            maskImages.forEach((mimg) => {
                mimg.addLayer()
            })

            return true
        }

        let remove = () => {
            let index = this.segState.focusedSegIndex

            if (index == -1) {
                return false
            }

            this.onstart(false)

            this.segState.segments.splice(index, 1)
            this.segState.focusedSegIndex--

            this.onload()

            maskImages.forEach((mimg) => {
                mimg.removeLayer(index)
            })

            return true
        }

        let moveUp = () => {
            let index = this.segState.focusedSegIndex

            if (index < 1) {
                return false
            }

            this.onstart()

            let tmp = this.segState.segments[index - 1]
            this.segState.segments[index - 1] = this.segState.segments[index]
            this.segState.segments[index] = tmp

            this.onload()

            return true
        }

        let moveDown = () => {
            let index = this.segState.focusedSegIndex

            if (index == -1 || index == this.segState.segments.length - 1) {
                return false
            }

            this.onstart()

            let tmp = this.segState.segments[index + 1]
            this.segState.segments[index + 1] = this.segState.segments[index]
            this.segState.segments[index] = tmp

            this.onload()

            return true
        }

        let moveTop = () => {
            if (index < 1) {
                return false
            }

            this.onstart()

            let tmp = this.segState.segments[index]
            this.segState.segments.splice(index, 1)
            this.segState.segments.splice(0, 0, tmp)

            this.onload()

            return true
        }

        let moveBottom = () => {
            let index = this.segState.focusedSegIndex

            if (index == -1 || index == this.segState.segments.length - 1) {
                return false
            }

            this.onstart()

            let tmp = this.segState.segments[index]
            this.segState.segments.splice(index, 1)
            this.segState.segments.splice(this.segState.segments.length, 0, tmp)

            this.onload()

            return true
        }

        this.process = () => {
            if (!this.enable) {
                return
            }

            switch (this.state) {
                case this.mode.CREATE:
                    create()
                    break
                case this.mode.REMOVE:
                    remove()
                    break
                case this.mode.MOVEUP:
                    moveUp()
                    break
                case this.mode.MOVEDOWN:
                    moveDown()
                    break
                case this.mode.MOVETOP:
                    moveTop()
                    break
                case this.mode.MOVEBOTTOM:
                    moveBottom()
                    break
            }

        }
    }
}

class Plot {

    static getInstance = function () {
        return new Histogram()
    }

    constructor() {
        // set the dimensions and margins of the graph
        let margin = { top: 10, right: 10, bottom: 20, left: 60 }

        this.loadView = function (domElement, data, colormap) {

            domElement.innerHTML = ''

            let width = domElement.clientWidth - margin.left - margin.right
            let height = domElement.clientHeight - margin.top - margin.bottom

            // append the svg object to the specified element
            let svg = d3.select('#' + domElement.id)
                .append('svg')
                .attr('width', width + margin.left + margin.right)
                .attr('height', height + margin.top + margin.bottom)
                .append('g')
                .attr('transform',
                    'translate(' + margin.left + ',' + margin.top + ')');

            // X axis: scale and draw:
            let x = d3.scaleLinear()
                .domain([1, 256])     // except 0 value pixels
                .range([0, width]);

            svg.append('g')
                .attr('transform', 'translate(0,' + height + ')')
                .call(d3.axisBottom(x));

            // Y axis: scale and draw:
            let y = d3.scaleLinear()
                .range([height, 0])
                .domain([0, d3.max(dataY, function (d) { return d.length; })]);

            svg.append('g')
                .call(d3.axisLeft(y).tickFormat(d3.format('d')));

            // append the bar rectangles to the svg element
            svg.selectAll('rect')
                .data(data)
                .enter()
                .append('circle')
                .attr('class', 'dot')
                .attr('r', '1')
                .attr('cx', function (d) {

                })
                .attr('cy', function (d) {

                })
                .style('fill', function (d, i) {
                    let hex = colormap[i].toString(16)
                    if (i < 16) {
                        hex = '0' + hex
                    }
                    return '#' + hex + hex + hex
                })
        }
    }
}

class Histogram {

    static instance = null

    static getInstance = function () {
        if (Histogram.instance == null) {
            Histogram.instance = new Histogram()
        }
        return Histogram.instance
    }

    constructor() {
        // set the dimensions and margins of the graph
        const markerRange = 10
        const totalMarkerCount = 25
        let margin = { top: 10, right: 10, bottom: 20, left: 30 }
        let marker = []
        let colormap = new Array(256).fill(0).map((value, index) => {
            return index
        })

        let convertHex = function (value) {
            if (value < 16) {
                return '0' + value.toString(16)
            }

            return value.toString()
        }

        this.loadView = function (domElement, data, logEnable = false) {

            domElement.innerHTML = ''

            let width = domElement.clientWidth - margin.left - margin.right
            let height = domElement.clientHeight - margin.top - margin.bottom

            // append the svg object to the specified element
            let svg = d3.select('#' + domElement.id)
                .append('svg')
                .attr('width', width + margin.left + margin.right)
                .attr('height', height + margin.top + margin.bottom)
                .append('g')
                .attr('transform',
                    'translate(' + margin.left + ',' + margin.top + ')');

            // X axis: scale and draw:
            let x = d3.scaleLinear()
                .domain([1, 256])     // except 0 value pixels
                .range([0, width]);

            let repaint = function () {
                d3.selectAll()

            }

            let drag = d3.drag()
                .on('drag', function (evt) {
                    let pos = d3.pointer(evt, this)[0]
                    let step = markerRange * width / 255
                    if (pos > step && pos < width - step) {
                        d3.select(this).style('x', pos)
                    }
                })

            svg.append('g')
                .attr('transform', 'translate(0,' + height + ')')
                .call(d3.axisBottom(x));

            // set the parameters for the histogram
            let histogram = d3.bin()
                .domain(x.domain())
                .thresholds(x.ticks(256));

            // apply this function to data to get the bins
            console.log()
            let bins = histogram(data);

            // Y axis: scale and draw:
            let y = d3.scaleLinear()
                .range([height, 0])
                .domain([0, d3.max(bins, function (d) {
                    if (logEnable) {
                        return Math.log(d.length + 1);
                    }
                    return d.length;
                })]);

            svg.append('g')
                .call(d3.axisLeft(y).tickFormat(d3.format('d')));

            // append the bar rectangles to the svg element
            svg.selectAll('rect')
                .data(bins)
                .enter()
                .append('rect')
                .attr('transform', function (d) {
                    let trans_y = d.length

                    if (logEnable) {
                        trans_y = Math.log(trans_y + 1)
                    }

                    return 'translate(' + x(d.x0) + ',' + y(trans_y) + ')';
                })
                .attr('width', function (d) { return x(d.x1) - x(d.x0); })
                .attr('height', function (d) {

                    let trans_y = d.length

                    if (logEnable) {
                        trans_y = Math.log(trans_y + 1)
                    }

                    return height - y(trans_y)
                })
                .style('fill', function (d, i) {
                    //console.log(colormap)
                    let hex = colormap[i].toString(16)
                    if (i < 16) {
                        hex = '0' + hex
                    }
                    return '#' + hex + hex + hex
                })
        }
    }
}

class TransferTools extends SegTools {
    constructor(segState, domElement) {
        super(segState)
        this.__enable = false
        this.mode = {
            HISTOGRAM: 0,
            COLORMAP: 1,
            APPLY: 2
        }
        //this.__hist = new Histogram()

        this.magData = null
        this.domElement = domElement
        this.domElement.style.position = 'relative'

        this.xLength = 256
        this.yLength = 512

        let transferCanvas = document.createElement('canvas')
        transferCanvas.style = 'width:100%;height:100%;'
        transferCanvas.width = this.xLength
        transferCanvas.height = this.yLength
        domElement.appendChild(transferCanvas)

        let markCanvas = document.createElement('canvas')
        markCanvas.style = 'width:100%;height:100%;position:absolute;top:0px;left:0px;'
        markCanvas.width = this.xLength
        markCanvas.height = this.yLength
        domElement.appendChild(markCanvas)

        let selectRect = { x: 0, y: 0, w: 0, h: 0 }
        let transfer_state = 0

        //this.plot = new Histogram()

        this.markContext = markCanvas.getContext('2d')
        this.displayContext = transferCanvas.getContext('2d')

        let penPos = [0, 0]
        markCanvas.addEventListener('mousedown', (evt) => {
            if (!this.enable) {
                return
            }

            transfer_state = 1

            penPos = getMousePos(markCanvas, evt)
            selectRect.x = penPos[0]
            selectRect.y = penPos[1]
        })

        markCanvas.addEventListener('mousemove', (evt) => {
            if (!this.enable) {
                return
            }

            if (transfer_state != 1) {
                return
            }

            penPos = getMousePos(markCanvas, evt)
            selectRect.w = penPos[0] - selectRect.x
            selectRect.h = penPos[1] - selectRect.y
            //console.log(selectRect)
            this.markContext.save()
            this.markContext.clearRect(0, 0, markCanvas.width, markCanvas.height)
            this.markContext.lineWidth = 4
            this.markContext.strokeStyle = 'red'
            this.markContext.strokeRect(selectRect.x, selectRect.y, selectRect.w, selectRect.h)
            this.markContext.restore()
        })

        markCanvas.addEventListener('mouseup', (evt) => {
            if (!this.enable) {
                return
            }

            if (transfer_state != 1) {
                return
            }

            penPos = getMousePos(markCanvas, evt)
            if (penPos[0] == selectRect.x && penPos[1] == selectRect.y) {
                this.markContext.clearRect(0, 0, markCanvas.width, markCanvas.height)
            }
        })

        window.addEventListener('mouseup', () => {
            if (!this.enable) {
                return
            }

            if (transfer_state != 1) {
                return
            }

            this.state = this.mode.COLORMAP
            this.process()

            transfer_state = 0
        })

        let scaleLinear = (array, size, logEnable = false) => {
            if (array.length <= 0) {
                return
            }

            if (logEnable) {
                for (let i = 0; i < array.length; i++) {
                    array[i] = Math.round(Math.log(array[i] + 1));
                }
            }

            //let min = 0
            let max = array[0];

            for (let i = 1; i < array.length; i++) {
                if (array[i] > max) {
                    max = array[i];
                }
            }

            //let diff = max - min
            let ratio = size / (max + 1);
            for (let i = 0; i < array.length; i++) {
                array[i] = Math.round((array[i] + 1) * ratio) - 1;
            }

        };

        let calculateGradients = () => {
            let imgData = this.segState.baseSegment.data;
            let dims = this.segState.baseSegment.dims;
            let operation = new Kernel();

            this.magData = imgData.slice;
            operation.gradientMagnitude(dims, imgData, this.magData);
            //console.log(this.magData)
            scaleLinear(this.magData, this.yLength, false);
        };

        let build2dHistogram = () => {
            //this.calculateFrequency()
            calculateGradients();

            //this.plot.loadView(this.domElement, this.segState.base.data)

            //return
            let baseData = this.segState.baseSegment.data;

            let ctxData = this.displayContext.getImageData(0, 0, this.xLength, this.yLength);

            for (let i = 0; i < ctxData.data.length * 4; i+=4) {
                ctxData.data[i] = ctxData.data[i + 1] = ctxData.data[i + 2] = 255;
                ctxData.data[i + 3] = 0;
            }

            let xIndex, yIndex, index;
            //console.log(baseData, this.magData, this.frequency)
            for (let i = 0; i < baseData.length; i++) {

                xIndex = parseInt(baseData[i] * (this.xLength - 1));

                yIndex = parseInt(this.yLength - 1 - this.magData[i]);

                index = this.xLength * yIndex + xIndex;
                index = 4 * index + 3;

                if (ctxData.data[index] < 50) {
                    ctxData.data[index] = 50;
                }
                
                ctxData.data[index]++;
            }

            //console.log(ctxData.data)
            this.displayContext.putImageData(ctxData, 0, 0);

            //let magLayout = document.getElementById('mag_layout')
            //this.__hist.loadView(magLayout, this.magData, true)
        };

        let applyHistogramData = () => {
            let segment = segState.focusedSegment;

            if (segment === null || !(segment instanceof Segment)) {
                transfer_state = 0;
                return;
            }

            let ylimit = { min: 0, max: 0 };
            let xlimit = { min: 0, max: 0 };

            ylimit.min = Math.min(selectRect.y, selectRect.y + selectRect.h);
            ylimit.max = Math.max(selectRect.y, selectRect.y + selectRect.h);

            xlimit.min = Math.min(selectRect.x, selectRect.x + selectRect.w);
            xlimit.max = Math.max(selectRect.x, selectRect.x + selectRect.w);

            let baseData = segState.baseSegment.data;
            let xIndex, yIndex;

            for (let i = 0; i < baseData.length; i++) {
                segment.data.setValue(0, i);
                xIndex = parseInt(baseData[i] * (this.xLength - 1));
                yIndex = parseInt(this.yLength - 1 - this.magData[i]);

                //console.log(xIndex, yIndex)
                if (xIndex < xlimit.min || xIndex > xlimit.max) {
                    continue
                }

                if (yIndex < ylimit.min || yIndex > ylimit.max) {
                    continue
                }


                segment.data.setValue(1, i);
            }
        };

        let buildColormap = () => { }

        this.process = () => {

            if (!this.enable) {
                return
            }

            this.onstart()

            switch (this.state) {
                case this.mode.HISTOGRAM:
                    build2dHistogram()
                    break
                case this.mode.COLORMAP:
                    buildColormap()
                    break
                case this.mode.APPLY:
                    applyHistogramData()
                    break
                default:
                    break
            }

            this.onload()

        }
    }

    set enable(option) {
        this.__enable = option
    }

    get enable() {
        return this.__enable
    }

}

export {ImageProcess, ListControlTools, BrushTools, ThresholdTools, GrowingTools, FilterTools, LogicTools, CropTools, SizeBasedTools, ScissorTools, TransferTools }